.download-button {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  margin: 10px;
  transition: background-color 0.3s ease-in-out;
}

.download-button:hover {
  background-color: #006ca9;
}

.export-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
